
import { Options, Vue } from "vue-class-component";

@Options({
	props: {
		address: String,
		isRegistered: Boolean,
		hasVoted: Boolean,
		votedProposalId: Number,
	}
})
export default class ListVoter extends Vue {
	address!: string;
	isRegistered!: boolean;
	hasVoted!: boolean;
	votedProposalId!: number;
	isCheck = false;
	get itemStatus(){
		return (this.isRegistered) ? 'list-group-item-success' : 'list-group-item-danger'
	}
}
